.footer {
    padding: 40px 0;
    font-size: rem-calc(16);
    @include breakpoint(xl) {
        padding: 70px 0;
    }
    .footer-widget {
        .widget-title {
            h4 {
                font-size: rem-calc(20);
                margin-bottom: 32px;
            }
            margin-bottom: 35px;
        }
        li {
            color: $white;
            font-size: rem-calc(16);
            margin-bottom: 10px;
        }
    }
    &-bottom {
        margin-top: 25px;
        @include breakpoint(lg) {
            margin-top: 55px;
        }
    }
    ul {
        li {
            &.menu-item {
                a {
                    transition: all 0.4s linear;
                    -webkit-transition: all 0.4s linear;
                    -moz-transition: all 0.4s linear;
                    -ms-transition: all 0.4s linear;
                    -o-transition: all 0.4s linear;
                    position: relative;
                    &::after {
                        content: "";
                        background: $white;
                        width: 0;
                        height: 2px;
                        position: absolute;
                        bottom: -6px;
                        left: 50%;
                        right: 50%;
                        display: block;
                        margin: 0 auto;
                        transition: all 0.4s linear;
                        -webkit-transition: all 0.4s linear;
                        -moz-transition: all 0.4s linear;
                        -ms-transition: all 0.4s linear;
                        -o-transition: all 0.4s linear;
                    }
                    &:hover {
                        &::after {
                            width: 100%;
                            left: 0;
                            right: 0;
                        }
                    }
                }
            }
        }
    }
}

.footer-logo {
    p {
        font-size: rem-calc(12);
        margin-bottom: 15px;
        @include breakpoint(lg) {
            font-size: rem-calc(16);
        }
    }
}

.contact-no {
    margin-bottom: 25px;
    .call {
        line-height: 1.3;
    }
    p {
        font-size: rem-calc(12);
        @include breakpoint(lg) {
            font-size: rem-calc(16);
        }
    }
}

.social-icon {
    li {
        a {
            border: 1px solid $brand-blue-100;
            height: 32px;
            width: 32px;
            line-height: 30px;
            text-align: center;
            border-radius: 50%;
            display: block;
            @include breakpoint(xs) {
                height: 40px;
                width: 40px;
                line-height: 38px;
            }
            @include breakpoint(xl) {
                height: 54px;
                width: 54px;
                line-height: 53px;
            }
        }
    }
}

.mobile-footer-menu {
    margin-top: 30px;
    font-size: rem-calc(12);
    @include breakpoint(lg) {
        font-size: rem-calc(16);
    }
}

.copyright {
    font-size: rem-calc(10);
    p {
        margin-bottom: 5px;
        @include breakpoint(lg) {
            margin-bottom: 10px;
        }
    }
    @include breakpoint(lg) {
        font-size: rem-calc(14);
    }
}

.footer-min {
    .footer-bottom {
        margin-top: 0;
        @include breakpoint(lg) {
            margin-top: 0;
        }
    }
}